<template>
    <el-table
            ref="singleTable"
            :data="walletList"
            v-loading="listLoading"
            element-loading-text="给我一点时间">
        <el-table-column
                v-for="item in tableHeader"
                :key="item.prop"
                :label="item.label"
                :prop="item.prop"
                :width="item.width"
                :min-width="item.minWidth"
                :align="item.align"
                :formatter="item.formatter"
                show-overflow-tooltip>
        </el-table-column>
    </el-table>
</template>
<script>
    import {statisticstotal} from '@/api/reporttab';

    export default {
        props: {
            width: {
                type: String,
                default: '200px'
            }
        },
        data() {
            return {
                walletList: [],
                listLoading: true,
                tableHeader: [
                    {
                        label: "资产名称",
                        prop: "typeName",
                    }, {
                        label: "总数",
                        prop: "sum",
                    }, {
                        label: "冻结数量",
                        prop: "freezeSum",
                    }
                ]
            }
        },
        computed: {
            tableDataLength() {
                return this.tableData.length;
            }
        },
        methods: {
            //查询所有列表以及条件查询
            getList() {
                this.listLoading = true;
                statisticstotal().then(response => {
                    this.walletList = response.data.walletList;
                    if (!response.result) {
                        this.msg = "未查询到此筛选数据";
                        this.$message({
                            type: 'warning',
                            message: this.msg
                        });
                    }
                    this.listLoading = false;
                })
            }
        }, created() {
            this.getList();
        }
    }
</script>



