import fetch from '@/utils/fetch'

export function statistics(query) {
    return fetch({
        url: '/admin/systemstatistics',
        method: 'get',
        params: query
    })
}

export function statisticstotal() {
    return fetch({
        url: '/admin/systemstatistics/total',
        method: 'get',
    })
}

export function virtualcoin(query) {
    return fetch({
        url: 'admin/virtualcoinandgoods',
        method: 'put',
        data: query
    })
}


